// Seat.js

import React from "react";
import { Button } from "@mui/material";

const Seat = ({ selected, onSelect, seatNumber, isBooked }) => {
  return (
    <Button
      variant="contained"
      color={isBooked ? "error" : (selected ? "success" : "primary")} // S dng mu  nu gh  c t
      onClick={onSelect}
      sx={{ m: 1 }}
    >
      {seatNumber} {/* Hin th s ch ngi */}
    </Button>
  );
};

export default Seat;
